     <h3>Leano</h3>
 
     Ke batla go leka go tlhoma khampani ya dikoloi e e tla tsamaisiwang mo Aforika.
     Mo nakong eno, nako e fetola didirisiwa tsotlhe tsa go tuka tsa makoloi ka dikoloi tsa motlakase.
     Ka jalo, koloi e ntsi ya bobedi, makoloi a a ngathetsweng a tla dirwa mo gae le kwa ntle ga naga.
     Sa ntlha, ke batla go leka go tsosa makoloi a a ntsi a bobedi le a a ngathetsweng gape mme ke a romele kwa dinageng tsa Aforika tse di sa ntseng di tlhoma.
     Naga ya Aforika e e humanegileng go gaisa tsotlhe ga e kgone go nna le koloi e le nngwe.
