    kuonyeshwa, na kudumishwa. Nafasi wazi itatoa mazingira ambapo tunaweza kusoma hisabati kwa umakini na kwa kufurahisha pamoja na ubao mweusi mkubwa uliowekwa. 
    <br> <br>

    <h3>Muundo wa SmileNumber Motors(Mfumo)</h3>

    SmileNumber Motors itatumia muundo wa wakati wa nyuma wa mchezo wa fumbo la slaidi. 
    Na itajazwa na upendo. Haijalishi jinsi kampuni ya magari inavyotengenezwa Afrika na nchi zinazoendelea, 
    ukubwa wa uchumi ni mdogo sana. Kwa maneno mengine, haijalishi unataka kununua kiasi gani, 
    hali za kiuchumi za kununua hazijafikiwa. Suluhisho la hili ni kutoa ajira na kazi zinazoweza kufanywa ndani ya nchi kila unapouza gari. 
    Kwa hivyo, unapouza gari, lazima pia utoe ajira za ndani na kazi. Kwa maneno mengine, muundo wa kampuni huunda athari za mtiririko wa mitaji zinazoundwa katika siku zijazo. 
    Kwa hivyo, ni njia ya kutumia algoriti halisi ya mtiririko wa wakati wa nyuma wa mchezo wa fumbo la slaidi. 
    Kwa hivyo nadhani ni upendo unapounda ajira na kazi zinazoweza kufanya kazi kwa wale wanaonunua magari. 
    Haijalishi jinsi kampuni ya magari inavyowekeza kote Afrika na kuzalisha 20-30, 
    haitakuwa na uwezo wa kudumisha kampuni ikiwa hakuna pesa. 
    Utaanzisha athari ya mitaji na mtiririko wa siku zijazo. 
    Kwa hivyo, huu ni muundo wa kampuni unaofafanua mtiririko wa kiuchumi na athari za wakati kinyume na tukio. 
    (Kihisabati, inaahidi tukio la uwezekano ambalo halitatokea kinyume na wakati katika utendaji wa kampuni. 
    Kwa maneno mengine, inaahidi tukio la uwezekano kinyume na wakati na kulifafanua katika mtiririko wa wakati.) 
    Kwa maneno mengine, ikiwa hakuna upendo kama kupita kwa wakati, muundo wa kampuni haujafafanuliwa. <br> <br>

    Wakati wa kuelewa algoriti ya mtiririko wa wakati wa nyuma ya mchezo wa fumbo la slaidi, 
    tunahitaji kufahamu kitu. 
    Kinyume na algoriti ya angavu ya mchezo wa fumbo la slaidi, 
    miundo yetu ya asili na kijamii hufafanua uwezekano ambao hautatokea kinyume na wakati. 
    Kwa hivyo, mfano hapo juu unafafanua upendo katika kupita kwa wakati kwa muda halisi. 
    Kwa hivyo, katika kupita kwa wakati, ikiwa hakuna upendo, muundo wa kampuni haujafafanui mtiririko wa kiuchumi au athari yoyote.

