    <div class="text-container" v-else-if="selectedLanguage === 'Rw'">
     
     <h1>"Mwigisha w'ibihe byose ni SmileNumber Motors"<br>
      <br>
     "SEKA! SEKA! SEKA!"</h1>
     <br>

     <p>
    <h3>Intego</h3>

    Ubuyobozi buhebuje, abakozi baseka, abakiriya banini baseka: icyerekezo cya SmileNumber Motors ni "gukomeza gucunga umunezero wawe."
    (i) Imibare yacu yabaseka izakomeza umunezero wawe binyuze mu micungire idahoraho yabakiriya.
    (ii) Umunezero utangirira ku Ishyirahamwe ryacu ryImibare ishimishije ukagera kuri wowe.
    (iii) Umunezero ukorwa na SmileNumber Motors uzakugeraho mu buryo butandukanye bwibicuruzwa, serivisi, n'ibikoresho.
    (iv) Binyuze mu mushinga wa Afurika ishimishije, uzahora useka.
    Intego yacu ya nyuma muri SmileNumber Motors ni ukuzana ibyishimo byacu, inseko, numunezero kuri wowe.
    Byoherezwa kuri wowe, kandi iyo umukiriya yongeye guseka, habaho uburyo bwo guhanahana hagati yabo.
    Duhanga kandi tukarema ibitekerezo nk'ibi. Intego yacu y'ibanze y'ubucuruzi ni ugushimisha abakiriya.
    Biragaragara ko abakiriya bashimishwa cyane iyo baseka ku bitekerezo na serivisi dutanga.
    Ushobora buri gihe kubona inseko zacu ahantu hose ku isi ku giciro cyangwa serivisi ihwanye nayo.
    Ishyirahamwe ryImibare ishimishije rishinzwe gucunga umunezero wawe numunezero. Hano hari icyitegererezo cyiminsi mikuru myinshi yumunezero, inama, nibirori bigomba gukorwa.
    SmileNumber Motors zacu zizagira ijambo ku isi nyuma ya Microsoft. <br> <br>

    <h3>Gahunda</h3>

    Ndashaka kugerageza gushinga uruganda rw'imodoka rukorera muri Afurika. 
    Ubu, ibihe birahindura moteri zose zisanzwe ziba imodoka z'amashanyarazi.
    Ku buryo, imodoka nyinshi zikoreshwa, imodoka zasenyutse zizakorwa mu gihugu no mu mahanga. 
    Icya mbere, ndashaka kugerageza kuzahura izo modoka zakoreshwaga nizindi modoka zasenyutse no kuzohereza mu bihugu bikennye muri Afurika. 
    Igihugu gikennye kurusha ibindi muri Afurika ntikibasha no kugira imodoka yumuntu. 
    Ku buryo, ntekereza ko ari ngombwa kongera ubukungu bw'icyo gihugu. Gutanga imodoka byongera gutanga, 
    kandi gutanga hatambaye kw'ubukungu.
    Mu yandi magambo, gutanga imodoka zihagije muri Afurika no kuzohereza bizazana ubuzima bushya mu bukungu bwa Afurika. 
    Ndatekereza ko igipimo cyubukungu bwibyo bihugu kizaba kiri hasi cyane ku buryo batazashobora kugura ikintu icyo ari cyo cyose uretse kugenda.
     Kuva icyo gihe, hazabaho gushaka cyane imodoka. Ntekereza ko bizaba. 
     Kugeza ubwo, tugomba gutanga serivisi zinyuma zumutima namahirwe yo gusana imodoka twohereza. 
     Mu mpera, abana b'Afurika bazarya neza banywe amazi meza. 
     Ahubwo y'uko kugurisha imodoka, tuzashyira umwihariko kuri serivisi z'inyuma z'ubunyangamugayo no gusubiramo imodoka. 
     Mu yandi magambo, imiterere y'ikigo ikora ibyasohoka mu buryo bw'algoriti ya puzzle mu buryo bw'ibinyuranye. 
     Nicyo ntekereza nk'igikorwa cy'urukundo. Uburyo bwo kugurisha hanze imodoka iseka nayo ni sisitemu y'urukundo. 
     Bisobanura ko hazashorwa imbaraga nyinshi mu gukora byinshi. Byongeye kandi, bizazana ibiciro byiyongera. 
     Ariko, urubuga rwo kugurisha hanze rwa SmileNumber Motors ruzakora mu buryo bwo kubaka uburambe no kugira icyizere imbere yabashoramari ba mbere. 
     Uburyo bwamafaranga namakampani anyura muri uru rukundo kugirango akore akazi nimirimo muri Afurika buzashyirwa mu rwego rwubumwe kugeza igihe cyose kizamara cyuzuye urukundo. 
     <br> <br>

    <h3>Ikigo cyImibare</h3>

    Ikigo cya SmileNumber Motors giteganya gukora ikigo cyubushakashatsi bwimibare. 
    Icya mbere, ndateganya gushyiraho itsinda ryubushakashatsi ryabaganga bari mu kiciro cyicyiciro cya nyuma cyicyiciro cya kaminuza bamaze kubona dogiteri mu mibare. 
    Kandi ndateganya gutegura amasomo menshi yibyishimo hamwe nabarimu ba matematike bageze mu kiruhuko. 
    Icya mbere, ndateganya gukodesha uruganda ruto rwatawe cyangwa ububiko no kuvugurura ahongaho. 
    Mu yandi magambo, ni ikigo cyubushakashatsi bwimibare yo mu bubiko. 
    Nyuma y'uko ikigo kimaze guhagarara, ndateganya gushyiraho ikigo cyubushakashatsi bwimibare, SmileNumber Motors, 
    hamwe n'ububiko bunini nka hangar yindege nigitabo kinini cyibitabo bya matematike. 
    Ubu, kizatanga cafe y'ubusomyi nahantu hameze nko mu isomero hamwe nakabari k'ahantu imodoka zishobora kubikwa, 
    kwerekanwa, no kubungabungwa. Ahantu hafunguye hazatanga ibidukikije aho dushobora kwiga imibare mu buryo bukomeye kandi bushimishije hamwe n'urukuta runini rw'umukara rwashyizweho. 
    <br> <br>

    <h3>Imiterere ya SmileNumber Motors(Sisitemu)</h3>

    SmileNumber Motors izashyiramo uburyo bwimiterere bwumwanya wo gusubira inyuma ku mukino wa puzzle. 
    Kandi izuzura urukundo. Ntacyo bimaze uko uruganda rwimodoka rukorewe muri Afurika no mu bihugu bikiri mu nzira yamajyambere, 
    igipimo cyubukungu ni gito cyane. Mu yandi magambo, ntacyo bimaze uko ushaka kugura, 
    uburyo bwo kugura ntabwo bushobora kugerwaho. Igisubizo cyibi ni ugutanga akazi n'akazi gashobora gukorwa mu gihugu hose igihe cyose ugurishije imodoka. 
    Kubwibyo, iyo ugurishije imodoka, ugomba no gutanga imirimo n'imirimo yo mu gihugu. Mu yandi magambo, imiterere y'ikigo ikora ingaruka z'imitungo y'imitungo izakorwa mu gihe kizaza. 
    Kubwibyo, ni uburyo bwo gushyiramo uburyo bw'algoriti bw'umwanya wo gusubira inyuma ku mukino wa puzzle. 
    Ku buryo ntekereza ko ari urukundo iyo ukora akazi nimirimo ishobora gukorwa n'abagura imodoka. 
    Ntacyo bimaze uko uruganda rwimodoka rushora muri Afurika hose rugakora 20-30, 
    ntiruzashobora gusigasira uruganda igihe nta mafaranga ahari. 
    Uzashyiraho ingaruka z'imitungo n'imitungo yigihe kizaza. 
    Kubwibyo, iyi ni imiterere y'ikigo isobanura igipimo cyubukungu n'ingaruka z'igihe mu buryo bwibinyuranye nibyabaye. 
    (Mu mibare, isezeranya igikorwa gishoboka kitazaba mu buryo bw'igihe mu gihe cyikorwa ry'ikigo. 
    Mu yandi magambo, isezeranya igikorwa gishoboka mu buryo bw'igihe n'icyo isobanura mu gihe cy'igihe.) 
    Mu yandi magambo, niba nta rukundo nka uko igihe gihita, imiterere y'ikigo ntabwo isobanurwa. <br> <br>

    Mu gihe cyo gusobanukirwa algoriti y'umwanya wo gusubira inyuma ku mukino wa puzzle, 
    tugomba kuba dufite ubumenyi bwikintu. 
    Mu buryo bwibinyuranye n'uburyo bw'algoriti y'ibintu by'ibintu byo gusubira inyuma, 
    imiterere yacu y'umwimerere n'imibereho isobanura uburyo butabaho mu buryo bwibinyuranye n'igihe. 
    Kubwibyo, icyitegererezo cyavuzwe haruguru gisobanura urukundo mu gihe cy'igihe mu gihe nyacyo. 
    Kubwibyo, mu gihe cy'igihe, niba nta rukundo, imiterere y'ikigo ntisobanura uburyo ubwo aribwo bwose bwubukungu cyangwa ingaruka.

