<script>
import RainbowMenu from '@/views/RainbowMenu.vue';
import MapVue from '@/views/MapVue.vue';
import MapSearchComponent from '@/components/MapSearchComponent.vue';

export default {
  name: 'Bible',
  components: {
    RainbowMenu,
    MapVue,
    MapSearchComponent,
  },
  data() {
    return {
      links: {
        1: 'https://www.instagram.com/smilenumbermathematics/',
        2: 'https://www.instagram.com/smilenumbermathematics/',
        3: 'https://www.instagram.com/smilenumbermathematics/',
        4: 'https://www.instagram.com/smilenumbermathematics/',
      },
      stars: []
    };
  },
  mounted() {
    this.randomizeStars();
  },
  methods: {
    navigate(pointNumber, starIndex) {
      if (this.links[pointNumber]) {
        window.open(this.links[pointNumber], '_blank');
      }
    },
    randomizeStars() {
      const starRefs = this.$refs.starRefs;
      const positions = [];

      starRefs.forEach((star, index) => {
        const newPosition = this.getRandomPosition(positions);
        star.style.position = 'absolute';
        star.style.top = `${newPosition.top}px`;
        star.style.left = `${newPosition.left}px`;
        positions.push(newPosition);
      });
    },
    getRandomPosition(positions) {
      const containerWidth = this.$el.clientWidth;
      const containerHeight = this.$el.clientHeight;
      const starWidth = 30; // Width of star element in pixels
      const starHeight = 30; // Height of star element in pixels

      let top = Math.random() * (containerHeight - starHeight);
      let left = Math.random() * (containerWidth - starWidth);

      // Check for collisions with existing positions
      positions.forEach(pos => {
        while (
          top > pos.top - starHeight &&
          top < pos.top + starHeight &&
          left > pos.left - starWidth &&
          left < pos.left + starWidth
        ) {
          top = Math.random() * (containerHeight - starHeight);
          left = Math.random() * (containerWidth - starWidth);
        }
      });

      return { top, left };
    }
  }
};
</script>

