            humanRights1: "4.1. Tiriso ya bana: Dinaga tse dintsi tse di tlhabologang di bona bana ba patelediwa go direla mo boemong jwa go tsena sekolong ka ntlha ya mathata a ikonomi.",
            humanRights2: "4.2. Lenyalong la bana: Mo dikarolong dingwe, bana ba gantsi ba patelediwa go tsena lenyalong ba sa le basha. Seno se amoga basetsana menyetla ya thuto mme se kotsi mo boitekanelong le polokelong ya bone.",
            humanRights3: "4.3. Thubakanyo le pogiso: Bana gantsi ba itemogela thubakanyo le pogiso mo magaeng a bone kgotsa mo sethabeng, se se amang boitekanelo jwa bone jwa mmele le maikutlo.",
            efforts1: "5.1. Tshegetso go tswa mo ditlhopheng tsa bodithabathaba le NGOs: Ditlhopha jaaka UNICEF, World Vision, Save the Children, le tse dingwe tse dintsi di dira diporojeke tse di farologaneng go tshegetsa bana mo Aforika le dinaga tse di tlhabologang. Maiteko a tseno a akaretsa go fana ka dijo, go tokafatsa go tsena mo metseng e e phepa, go tokafatsa didirisiwa tsa thuto, le go sireletsa ditshwanelo tsa bana.",
            efforts2: "5.2. Maiteko go tswa mo sethabeng sa gae: Dithaba tse dintsi tsa gae di tsaya dikgato go rarabolola mathata a. Mehlala e akaretsa go tlhoma dikolo tsa sethaba, go tokafatsa mekgele ya temo ya lehae, le go atolosa ditshwanelo tsa basadi go tsena thutong.",
            conclusion: "Mathata a ke a mantsi mme a tseneletseng, mme a ka tokafadiwa go bokoa ka tlhokomelo e e tswelelang ya bodithabathaba le tshegetso, le go tsenelela ga sethaba sa gae."
